<template>
  <div class="about">
    <img src="../assets/LOGO1.png" alt="logo churrasco">
    <h1>Ruta protegida por JWT</h1>
    <h2>Lo lograste! te logueaste bien</h2>
    <h3>Tu token es:</h3> <br>
    {{token}}
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default({
  computed: {
    ...mapState(['token'])
  },
  methods:{
    async datosProtegidos(){
      try {
        const res = await fetch('https://jwt-gabriel-api.herokuapp.com/api/admin/', {
          headers: {
            'Content-Type': 'application/json',
            'auth-token': this.token
          }
        })
        const resDB= await res.json()
        console.log(resDB)
      } catch (error) {
        console.log(error)
      }
    }
  },
  created(){
    this.datosProtegidos()
  }
})
</script>
